html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.logo__name, h2, .bigger, .middle {
  text-transform: uppercase;
  color: #e8603c;
  font-family: 'HelveticaNeue', sans-serif;
  font-weight: bold;
}

h6 {
  font-size: 16px;
  font-family: 'Bebas', regular;
}

a {
  text-decoration: none;
  color: #b9b9b9;
  font-family: 'HelveticaNeue', sans-serif;
}

.wrapper {
  max-width: 1400px;
  min-width: 1000px;
  padding-bottom: 300px;
  margin: 0 auto;
}

.gray-light {
  color: #b9b9b9;
}

.small {
  margin-bottom: 10px;
  font-size: 12px;
  font-family: 'HelveticaNeue', sans-serif;
}

.medium {
  font-size: 14px;
  font-family: 'HelveticaNeue', sans-serif;
}

.middle {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 400;
  font-family: 'Bebas', regular;
}

.bigger {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 400;
  font-family: 'Bebas', regular;
}

header {
  width: 70%;
  margin: 0 auto;
}

header .logo {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
  padding: 0 10px;
}

header .logo__name {
  font-family: 'Bebas', regular;
  font-size: 36px;
}

header .logo__name span {
  color: #f3af9d;
  margin-left: -3px;
}

header .logo__nav {
  width: 390px;
}

header .logo__nav ul {
  display: flex;
  justify-content: space-between;
  margin-top: -20px;
}

header .logo__nav a {
  text-transform: uppercase;
  font-size: 16px;
  font-family: 'Bebas', regular;
}

header .logo__nav a:hover {
  color: #e8603c;
}

.color-bg {
  background-color: #e8603c;
}

.section {
  display: flex;
  justify-content: space-between;
}

.section .aside-main, .section .aside-secondary {
  width: 15%;
}

.section .aside-main__img {
  padding-right: 5px;
  float: right;
  cursor: pointer;
}

.section__nav {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}

.section__nav--list {
  height: 2.5em;
  width: 2.5em;
  margin-right: 10px;
  color: #fff;
  cursor: pointer;
  background: #f3af9d;
  text-align: center;
  line-height: 2.5em;
  font-size: 16px;
  font-weight: bold;
  font-family: 'HelveticaNeue', sans-serif;
}

.section__nav--list:hover {
  background: #e8603c;
}

.empty-block {
  width: 15%;
  background-color: #f1ad9b;
}

.top {
  margin-bottom: 40px;
}

.main-content {
  width: 70%;
  display: flex;
  justify-content: space-between;
  margin: 0 10px;
}

.main-content__name {
  margin: 15px;
  text-transform: uppercase;
  color: #fff;
  font-family: 'Bebas', regular;
  font-size: 24px;
}

.main-content__block {
  width: 70%;
  display: flex;
  align-items: center;
}

.main-content__block span {
  display: block;
  background: url("../img/double_quotes.png") no-repeat;
  background-size: contain;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  float: left;
}

.main-content__block--text {
  padding: 20px;
  background: #f2f2f2;
}

.main-content .aside {
  width: 25%;
}

.main-content .aside__categories {
  margin-bottom: 10px;
}

.main-content .aside__categories p {
  margin-bottom: 0;
}

.main-content .aside__caption {
  display: flex;
  justify-content: space-between;
}

.main-content .aside__caption--text {
  display: block;
  width: 100%;
}

.main-content .aside__caption--separator {
  display: block;
  margin-top: 4px;
  background: url("../img/separator.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  height: 16px;
  width: 100%;
}

.main-content .blog {
  width: 70%;
  display: flex;
  flex-direction: column;
}

.main-content .blog__info {
  display: flex;
  justify-content: space-between;
  padding-right: 5%;
}

.main-content .blog__info--info {
  width: 20%;
  height: 200px;
}

.main-content .blog__info--desc {
  width: 80%;
}

.main-content .blog .img__picture {
  width: 100%;
  height: 100%;
}

.photo__gallery {
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
}

.photo__gallery--img {
  width: 50px;
  height: 50px;
  margin: 5px;
  background: #f3af9d;
  border: 1px solid #e8603c;
}

.separator {
  width: 70%;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.separator img {
  width: 70%;
  margin: 40px 0;
}
